$max-width: 860px;
$min-width: 320px;

*,*:before, *:after {
  box-sizing:border-box;
  font-family: Roboto, Helvetica, Arial, sans-serif;
}


.App {
  display: block;
  width: 100%;
  height: 100%;
  margin: auto;
}

.container {
  width: 100%;
  max-width: $max-width;
  min-width: $min-width;
  margin: auto;
}
