$width-button:20%;

.uploader {
  padding: 1%;

  form{
    margin-left:10%;
    top:calc(80% - 50px - 50px);
    width:80%;

  input{
    width:calc(100% - #{$width-button} - 175px);
    padding: 5px;
    //border: 1px solid #999;
    border: 1px solid rgba(25, 118, 210, 0.5);
    border-radius: 3px;
    box-shadow: 0 0 1px 1px rgba(0,0,0,0);
    transition: .17s linear;
    height: 1em;
    margin: 0px 10px 0px 0px;
    padding: 17px 14px;
    vertical-align: middle;
    &:focus {
      outline: none;
      box-shadow: 0 0 1px 1px #4d90f0;
    }
    &:focus::-moz-placeholder {
      color: transparent;
    }
    &:focus::-webkit-input-placeholder {
      color: transparent;
    }
    &::-webkit-input-placeholder {
      color: #767676;
      opacity: 1;
    }

    &:-moz-placeholder { /* Firefox 18- */
      color: #767676;
      opacity: 1;
    }

    &::-moz-placeholder {  /* Firefox 19+ */
      color: #767676;
      opacity: 1;
    }

    &:-ms-input-placeholder {
      color: #767676;
      opacity: 1;
    }
}
  label{
    margin-right:10px;
  }
  span{
    color:red;
    padding-top: 20px;
  }

  button{
    width:$width-button;
   // background: url(img/upload_file.png) no-repeat;
    display: inline-flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    position: relative;
    -webkit-tap-highlight-color: transparent;
    background-color: transparent;
    outline: 0px;
    margin: 0px;
    cursor: pointer;
    user-select: none;
    vertical-align: middle;
    appearance: none;
    text-decoration: none;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    min-width: 64px;
    padding: 5px 15px;
    border-radius: 4px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border: 1px solid rgba(25, 118, 210, 0.5);
    color: rgb(25, 118, 210);
    &.raised{
      //@include transition(0.1s all);
      background: red;
      //box-shadow: 0px 1px 1px darken(red, 15%);
      box-shadow: 0px 1px 1px red;
    }
    &.active{
      //background: darken(red, 2,5%);
      background: red;
      box-shadow: 0px 1px 1px red;
    }
    }
  }
}


