.popupParent {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.5);
}

.popupImage {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 10vh;
    //bottom: 10vh;
    margin: auto;
    max-height: 300px;
}

.popupImage img{
    width: 80vh;
}


.imageClosingButton,.imageDeletingButton {
    position: relative;
}

.drag-area{
    padding: 10px;
    height: 10vh;
    border: 5px dashed #7649bc;
    outline: none;
    cursor: pointer;
}
